@import '../../common/styles.scss';

.tool-bar {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    width: 80vw;
    font-family: $font-primary;
    padding: 10px;
    gap: 20px;
}

.search {
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    gap: 5px;
}

.search > input{
   height: 100%;
}